.contact-container {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.contact-container h1 {
  font-size: 60px;
  margin-top: 0;
  margin-bottom: 1rem;
}

.socials {
  width: 35%;
  height: 60vh;
}

.socials p {
  font-size: 20px;
  font-weight: 100;
}

.socials ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  gap: 1rem;
}

.socials a {
  color: black;
  font-size: 2rem;
}

.socials a:hover {
  color: rgb(248, 28, 28);
  font-size: 2rem;
}

.contact-form {
  width: 30%;
  height: 60vh;
  /* box-shadow: 0 0 5px 0; */
  padding: 2rem 2rem;
}

.contact-form span {
  color: black;
}

.input-container label {
  font-weight: 500;
}

.contactInput {
  border: 1px solid white;
  border-radius: 25px;
  padding: 0.5rem;
  margin-top: 0.5rem;
  box-shadow: 0 0 5px 0;
  width: 100%;
  font-family: "Hind Madurai", sans-serif;
}

.contact-form textarea {
  border: 1px solid white;
  border-radius: 25px;
  padding: 1rem;
  margin-top: 0.5rem;
  max-width: 96%;
  min-width: 96%;
  max-height: 20vh;
  min-height: 20vh;
  box-shadow: 0 0 5px 0;
  font-family: "Hind Madurai", sans-serif;
}

.submit {
  border: 1px solid rgb(248, 28, 28);
  border-radius: 25px;
  color: whitesmoke;
  font-weight: bold;
  padding: 0.5rem;
  background-color: rgb(248, 28, 28);
  width: 105%;
}

.submit:hover {
  border: 1px solid rgb(248, 28, 28);
  color: rgb(248, 28, 28);
  background-color: whitesmoke;
}

.submit-message {
  padding-top: 0.5rem;
  text-align: center;
  color: green;
}

@media (max-width: 800px) {
  .contact-container {
    height: auto;
    flex-direction: column;
    padding-bottom: 10vh;
  }

  .socials {
    width: 75%;
    height: auto;
    justify-content: flex-end;
  }

  .contact-form {
    width: 75%;
  }
}

@media (max-width: 500px) {
  .socials {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
  }

  .contact-form {
    width: 65%;
    padding: 0;
    padding-top: 1rem;
    height: auto;
  }

  .contactInput {
    width: 91.5%;
  }

  .contact-form textarea {
    max-width: 85%;
    min-width: 85%;
    max-height: 20vh;
    min-height: 20vh;
  }

  .submit {
    width: 100%;
  }
}
